@import url("https://fonts.googleapis.com/css2?family=Poppins&display=swap");
.snackbar-content {
  background-color: #04724d;
}
#client-snackbar {
  color: #ccc;
}
.client-button {
  margin-left: 63px;
}
.MuiButton-label {
  width: 300px;
  display: flex;
  align-items: inherit;
  justify-content: flex-end;
}
.MuiSnackbarContent-root {
  border-radius: 0px;
}
@media (min-width: 768px) {
  .MuiButton-label {
    width: 80px;
    display: flex;
    align-items: inherit;
    justify-content: flex-end;
  }
}
@media (min-width: 1280px) {
  .snackbar-content {
    background-color: #04724d;
    font-family: Poppins;
  }
}
@media (min-width: 1920px) {
  .MuiButton-label {
    width: 0px;
    display: flex;
    align-items: inherit;
    justify-content: flex-end;
  }
  .snackbar-content {
    background-color: #04724d;
  }
  .client-button {
    margin-left: 138px;
  }
}
